@import '../../../styles/variables';

.page {
    margin: auto;
    width: 90%;

    margin-bottom: 6vh;
    border-bottom: 1px solid lightgray;

    justify-content: center;
    align-items: center;

    @include mobile {
        width: 90%;
    }

    padding-bottom: 6vh;
}

.subtitle {
    color: gray;
    font-size: 18px;

    margin: auto;

    text-align: left;

    @include mobile {
        font-size: 16px;
        width: 100%;
    }
}

.subtitle:before {
    content: '-- '
}

.clientContainer {

    margin-left: 5%;
    margin-right: 5%;

    h2 {
        text-align: justify;
        font-size: 2rem;
        color: #666666;
        font-weight: 500;

        @include mobile {
            font-size: 1.1rem;
        }
    }

    h2:before {
        color: $red-bg;
        font-size: 2.2rem;
        font-style: italic;
        content: '"';

        @include mobile {
            font-size: 1.2rem;
        }
    }

    h2:after {
        color: $red-bg;
        font-size: 2.2rem;
        font-style: italic;
        content: '"';

        @include mobile {
            font-size: 1.2rem;
        }
    }
}

.imageContainer {
    display: flex;

    justify-content: center;
    align-items: center;
}

.image {
    @include mobile {
        width: 50%;
    }
}