@import '../../../styles/variables';

.page {
    margin: auto;
    width: 90%;

    margin-bottom: 6vh;
    border-bottom: 1px solid lightgray;

    justify-content: center;
    align-items: center;

    @include mobile {
        width: 90%;
    }

    padding-bottom: 6vh;
}

.subtitle {
    color: black;
    font-size: 18px;
    font-weight: 400;

    margin: auto;
    margin-bottom: 36px;

    text-align: center;

    @include mobile {
        font-size: 18px;
        width: 90%;
        margin-bottom: 12px;
        text-align: justify;
    }
}

.immersiveHeader {
    text-transform: uppercase;
    color: $text-color;
    font-size: 24px;
    margin: 24px 24px;

    text-align:center;

    @include mobile {
        text-align: justify;
    }
}

.imageContainer {
    display: flex;
    column-gap: 5%;
    flex-wrap: wrap;

    margin: auto;
    width: 100%;

}

.image {
    width: 30%;
    justify-content: center;
    align-items: center;

    @include mobile {
        width: 100%;
    }

    h2 {
        font-size: 20px;
        text-align: justify;
        font-weight: 400;

        @include mobile {
            width: 90%;
            font-size: 20px;

            padding-left: 5%;
            padding-right: 5%;
        }
    }

    img {
        width: 100%;
        height: 20vh;

        object-fit: contain;
    }
}